import React from 'react';
import Footer from '@/components/layout/Footer';
import EmpresaCarousel from '@/components/ui/EmpresaCarousel';

export default function Page() {
    return (
        <div className="min-h-screen bg-gradient-to-br from-blue-50 to-orange-50 flex flex-col">
            <main className="flex-grow container mx-auto px-4 py-12 space-y-16">
                <section id="demo" className="max-w-4xl mx-auto space-y-8 text-center pt-24">
                    <h1 className="text-3xl md:text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-green-600 to-blue-600">
                        Passo 2: Carrossel e Footer
                    </h1>
                    <p className="text-gray-600 text-lg">
                        Aqui está a demonstração do Footer e do Carrossel que estava na Hero section. O carrossel
                        mantém os tempos originais, a pausa visual no hover e utiliza a tag `next/image` como determinaste.
                    </p>
                    <div className="py-8">
                        <EmpresaCarousel />
                    </div>
                </section>
            </main>
            <Footer />
        </div>
    );
}
